<template>
 <div id="myList" class="myList">
    <ul>
      <li v-for="item in product" :key="item.goodsId">
        <el-popover placement="top">
          <p>确定删除吗？</p>
          <div style="text-align: right; margin: 10px 0 0">
            <el-button type="primary" size="mini" @click="deleteCollect(item.goodsId)">确定</el-button>
          </div>
          <i class="el-icon-close delete" slot="reference" v-show="isDelete"></i>
        </el-popover>
        <router-link :to="{ path: '/goods/details', query: {productID:item.goodsId} }">
          <img :src="item.goodsCoverImg" alt />
          <h2>{{item.goodsName}}</h2>
          <h3>{{item.goodsIntro}}</h3>
          <p>
            <span>{{item.sellingPrice}}金币</span>
            <span
              v-show="item.originalPrice != item.sellingPrice"
              class="del"
            >{{item.originalPrice}}金币</span>
          </p>
          <div class="buttonthis">
              <el-button type="primary" size="mini" >删除</el-button>
              <el-button type="success" size="mini" >修改</el-button>
          </div>
        </router-link>
      </li>
      
    </ul>
  </div>
</template>

<script>
export default {
  data(){
    return{
      product:"",
    }

  },
    created(){
      this.getPromo()

    },
    methods:{
       getPromo( ) {
       this.$axios(   
            {
                url: "http://localhost:3000/cart", 
            //   url: this.$target+"/goodsByCategoryId", 
              method: 'get',
              params:{
               currentPage: 1,
              pageSize: 5,
              }
        })
        .then(res => {
           this.product = res.data.data;
         })
        .catch(err => {
          return Promise.reject(err);
        });     
    }
  }
}
</script>

<style scoped>
.myList ul li {
  z-index: 1;
  float: left;
  width: 234px;
  height: 300px;
  padding: 10px 0;
  margin: 0 0 14.5px 13.7px;
  background-color: white;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  position: relative;
}
.myList ul li:hover {
  z-index: 2;
  -webkit-box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -2px, 0);
}
.myList ul li img {
  display: block;
  width: 200px;
  height: 160px;
  background: url(../assets/imgs/placeholder.png) no-repeat 50%;
  margin: 0 auto;
}
.myList ul li h2 {
  margin: 25px 10px 0;
  font-size: 14px;
  font-weight: 400;
  color: #333;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.buttonthis{
  margin: 5px 10px;
  height: 50px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.myList ul li h3 {
  margin: 5px 10px;
  height: 18px;
  font-size: 12px;
  font-weight: 400;
  color: #b0b0b0;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.myList ul li p {
  margin: 10px 10px 10px;
  text-align: center;
  color: #ff6700;
}
.myList ul li p .del {
  margin-left: 0.5em;
  color: #b0b0b0;
  text-decoration: line-through;
}
.myList #more {
  text-align: center;
  line-height: 280px;
}
.myList #more a {
  font-size: 18px;
  color: #333;
}
.myList #more a:hover {
  color: #ff6700;
}
.myList ul li .delete {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
}
.myList ul li:hover .delete {
  display: block
}
.myList ul li .delete:hover {
  color: #ff6700;
}
</style>
